@font-face {
    font-family: 'PTSansExpert';
    src: url('../font/PT-Sans-Expert_Regular/PT-Sans-Expert_Regular.eot');
    src:
        url('../font/PT-Sans-Expert_Regular/PT-Sans-Expert_Regular.eot?#iefix') format('embedded-opentype'),
        url('../font/PT-Sans-Expert_Regular/PT-Sans-Expert_Regular.woff2') format('woff2'),
        url('../font/PT-Sans-Expert_Regular/PT-Sans-Expert_Regular.woff') format('woff');
